import React from 'react';
import style from '../css/style.css'; 
import  logo  from '../img/logoC.png';
import firebase from 'firebase';
import {connect} from 'react-redux';
import { 
    modificaEmail,
    modificaSenha,
    autenticaUsuario,
} from '../redux/actions';

import { Link, Redirect } from 'react-router-dom';

class Login extends React.Component {
    constructor(props){
        super(props);        
        this.state = {
            nome:'',
            email: '',
            senha: '',
            redirect:'',
        }
        this.autenticaUsuario = this.autenticaUsuario.bind(this);
    }

    autenticaUsuario(){        
        const { email, senha } = this.state;
        this.props.autenticaUsuario({ email, senha });
    }

    render(){
        return (
            <div class="base login">
                <table class="popUpLogin">
                    
                    <tr>
                        <td>
                            <img class="logo" src={logo} />
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <h1>Área do Aluno</h1>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <p>Endereço de E-mail:</p>
                            <input  class="campoLogon" onChange={(e) => {this.setState({email:e.target.value})} }/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <p>Senha:</p>
                            <input  class="campoLogon" type="password" onChange={(e) => {this.setState({senha:e.target.value})} }/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <input class="botaoAction" type="submit" value="Entrar" onClick={this.autenticaUsuario} />
                            <h5 class="erro">{ this.props.erroLogin } </h5>
                            { this.props.redirect == 'Principal' 
                                ? <Redirect to="/Principal"> { console.log('Redireciona p Principal') }</Redirect>
                                : <Redirect to="/Login"> { console.log('redireciona p Login') }</Redirect> }
                        </td>
                    </tr>
                    <tr>
                        <td class="entrarCampo">
                            Ainda não tem registro? <Link to="/Cadastro"> <h3>Registre-se Agora!</h3></Link>
                        </td>
                    </tr>
                </table>
                
                
            </div>
        );
    }
}


const mapStateToProps = state => (
    {
        email : state.CalculosReducers.email,
        senha : state.CalculosReducers.senha,
        erroLogin: state.CalculosReducers.erroLogin,
        redirect: state.CalculosReducers.redirect,
    }
)

export default connect(mapStateToProps, {
    modificaEmail,
    modificaSenha,
    autenticaUsuario
})(Login); 