import React from 'react';
// eslint-disable-next-line
import style from '../css/style.css';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux';
import { 
    sair,
} from '../redux/actions';

class Header extends React.Component {    
    constructor(props){
        super(props);        
        this.state = {
            nome:'',
            email: '',
            senha: '',
            redirect:''
        }
        this.sair = this.sair.bind(this);
    }

    sair(){
        this.props.sair();
    }

    render(){
        return(
            <div class="header">
                <table border="0">

                    <tr>
                        <td>
                            <h1>Calculadora Pro Trader Investing</h1>
                        </td>
                        <td>
                            <Link to="/Login">
                                <p class="botaoSair">Sair</p>
                            </Link>
                        </td>
                    </tr>
                </table>
                
                <hr  />
            </div>

        );
    }
}

const mapStateToProps = state => (
    {
        redirect: state.CalculosReducers.redirect,
    }
)

export default connect(mapStateToProps, {
    sair
})(Header); 