import { 
    MODIFICA_BANCA,
    MODIFICA_PAYOUT,
    MODIFICA_RETORNO,
    MODIFICA_STOPLOSS,
    MODIFICA_ORDEM,
    MODIFICA_EMAIL,
    MODIFICA_NOME,
    MODIFICA_SENHA,
    CADASTRO_USUARIO_ERRO,
    CADASTRO_USUARIO_SUCESSO,
    AUTENTICA_USUARIO_ERRO,
    AUTENTICA_USUARIO_SUCESSO,
    SAIR
 } from '../actions'

const INITIAL_STATE = {
    nome:'',
    email:'',
    senha:'',
    erroCadastro: '',
    erroLogin:'',
    redirect:'',
    banca:0,
    payout:0,
    stopLoss:0,
    ordem:0,ordem2:0,ordem3:0,ordem4:0,ordem5:0,ordem6:0,ordem7:0,ordem8:0,ordem9:0,ordem10:0,ordem11:0,ordem12:0,ordem13:0,ordem14:0,ordem15:0,
    retorno:0,retorno2:0,retorno3:0,retorno4:0,retorno5:0,retorno6:0,retorno7:0,retorno8:0,retorno9:0,retorno10:0,retorno11:0,retorno12:0,retorno13:0,retorno14:0,retorno15:0,
    resultado:0,
}

const CalculosReducers = (state = INITIAL_STATE, action) => {
    console.log(action);
    switch(action.type){        
        case MODIFICA_EMAIL:
            return {
                ...state,
                email: action.payload
            }
        case MODIFICA_NOME:
            return {
                ...state,
                nome: action.payload
            }
        case MODIFICA_SENHA:
            return {
                ...state,
                senha: action.payload
            }
        case MODIFICA_BANCA:
            return {
                ...state,
                banca: action.payload
            }
        case MODIFICA_PAYOUT:
            return {
                ...state,
                payout: action.payload
            }
        case MODIFICA_RETORNO:
            return {
                ...state,
                retorno: action.payload
            }    
        case MODIFICA_STOPLOSS:
            return {
                ...state,
                stopLoss: action.payload
            }
        case MODIFICA_ORDEM:
            return {
                ...state,
                ordem: action.payload,
            }
        case CADASTRO_USUARIO_ERRO:
            return {
                ...state,
                erroCadastro: action.payload,
                redirect: 'Cadastro'
            }
        case CADASTRO_USUARIO_SUCESSO:
            return {
                ...state,
                redirect: 'Login'
            }
        case AUTENTICA_USUARIO_ERRO:
            return {
                ...state,
                erroLogin: action.payload,
            }    
        case AUTENTICA_USUARIO_SUCESSO:
            return {
                ...state,
                redirect: 'Principal'
            }
        case SAIR:
            return {
                ...state,
                email:'',
                senha:'',
                nome:''
            }    
        }   
    return state;
}
 
export default CalculosReducers;