
import React,  { Component } from 'react'
// eslint-disable-next-line
import style from '../css/style.css'
// eslint-disable-next-line
import { reduxForm, Field } from 'redux-form'
import {connect} from 'react-redux'
import {modificaBanca, modificaEmail, modificaSenha, modificaNome} from '../redux/actions'
import firebase from 'firebase';

import { Link } from 'react-router-dom';

class Teste extends Component {

    constructor(props){
        super(props);        
        this.state = {
            nome: 'nomeState',
            email: 'mailState',
            senha: 'senhaState'
        }
        this.noReload = this.noReload.bind(this);
        this.cadastrarUsuario = this.cadastrarUsuario.bind(this);
    }

    cadastrarUsuario(){
        console.log('testando função')
        //firebase.auth().createUserWithEmailAndPassword( this.state.email, this.state.senha )
        
    }

    noReload(e){
        e.preventDefault(this)
    }

    render(){
        return(
            <div>
                <form onSubmit={this.noReload}>
                    <div class="base login">
                        <table class="popUp">
                            <tr>
                                <td>
                                    <h4>Informe seus dados</h4>
                                    <p>Primeiro Nome:</p>
                                    <input value={ this.state.nome } class="campoLogon" onChange={(e) => {this.setState({nome:e.target.value})} } />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <p>Endereço de E-mail:</p>
                                    <input value={ this.state.email } class="campoLogon" onChange={(e) => {this.setState({email:e.target.value})} } />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <p>Senha:</p>
                                    <input value={ this.state.senha } class="campoLogon" onChange={(e) => {this.setState({senha:e.target.value})} } />
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <button type="submit" onClick={ this.logarUsuario() }>Entrar</button>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <button type="submit" onClick={ this.cadastrarUsuario() }>Cadastrar</button>
                                </td>
                            </tr>
                        </table>
                    </div>
                </form>
            </div>
        );
    }
}


const mapStateToProps = state => {
    console.log('estado: ' + state);
    return(
        {
            banca: state.CalculosReducers.banca,
            resultado: state.CalculosReducers.resultado,
            email: state.CalculosReducers.email,
            senha: state.CalculosReducers.senha,
            nome: state.CalculosReducers.nome,
        }
    );
}

export default connect(mapStateToProps, {
    modificaBanca,
    modificaEmail,
    modificaSenha,
    modificaNome
})(Teste); 